<template>
  <v-row justify="center">
    <v-pagination
      v-model="page"
      :length="length"
      total-visible="9"
      circle
      @input="input"
      @next="next"
      @previous="previous"
    ></v-pagination>
  </v-row>
</template>

<script>
export default {
  name: "Pagination",
  props: ["current", "length"],
  data: () => ({ page: null }),
  mounted() {
    this.page = this.current;
  },
  methods: {
    input(selectedPage) {
      this.$emit("change", selectedPage);
    },
    next() {
      this.$emit("change", this.page + 1);
    },
    previous() {
      this.$emit("change", this.page - 1);
    },
  },
};
</script>
